<template>
    <div class="create-link">
        <link-common @submitLinkForm="createNewLink"></link-common>
    </div>
</template>

<script>
import linkCommon from './linkCommon/linkCommon';
export default {
    name: 'createLink',
    components: {
        linkCommon
    },
    data() {
        return {
            prevLinkForm: {}
        }
    },
    methods: {
        createNewLink(linkForm) {
            this.$axios.post('/api/link/create', linkForm)
            .then(res => {
                console.log(res.data)
                this.$Message.info('链接创建成功')
                // this.$router.push({path: '/', 
                //     name: 'Index',
                //     params: {
                //         linkForm: linkForm
                //     }
                // })
                window.location.href = '/statistics'

            })
        }
    }
}
</script>



