<template>
    <div class="link-common">
        <Form class="form-cont" :model="linkForm" :label-width="150">
            <FormItem label="创建统计链接的时间">
                <DatePicker v-model="createtime" @on-ok="confirmDate" type="datetime" placeholder="选择日期和时间" style="width: 200px"></DatePicker>
            </FormItem>
            <FormItem label="选择小说平台">
                <Select v-model="platform" placeholder="选择小说平台">
                    <Option value="6">袋鼠</Option>
                </Select>
            </FormItem>
            <FormItem label="小说平台的推广链接">
                <Input v-model="linkForm.url" placeholder="输入小说平台的推广链接"></Input>
            </FormItem>
            <FormItem label="微信外的落地页链接">
                <Input v-model="linkForm.out_url" placeholder="输入微信外的落地页链接"></Input>
            </FormItem>
            <FormItem label="渠道名称">
                <Input v-model="linkForm.qudao" placeholder="输入渠道名称"></Input>
            </FormItem>
            <FormItem label="服务号名称">
                <Input v-model="linkForm.fuwuhao" placeholder="输入服务号名称"></Input>
            </FormItem>
            <div class="btn-cont">
                <Button type="success" @click="submitLinkForm">提交</Button>
                <Button type="warning" @click="cancel">取消</Button>
            </div>
        </Form>
    </div>
</template>

<script>
export default {
    props: {
        prevLinkForm: Object
    },
    data() {
        return {
            linkForm: {},
            createtime: null,
            platform: ''
        }
    },
    mounted() {
        if(this.prevLinkForm) {
            console.log(this.prevLinkForm)
            this.linkForm = this.prevLinkForm
            this.platform = this.linkForm.platform.toString()
            this.createtime = new Date(this.linkForm.createtime)
            this.linkForm.id = this.linkForm._id
        }
    },
    methods: {
        confirmDate() {
            var date = new Date(this.createtime).getTime();
            this.linkForm.createtime = date
        },
        submitLinkForm() {
            this.linkForm.platform = Number(this.platform)
            var date = new Date(this.createtime).getTime();
            this.linkForm.createtime = date
            this.$emit('submitLinkForm', this.linkForm)
            this.linkForm = {}
        },
        cancel() {
            this.linkForm = {}
            this.$router.push('/statistics')
        }
    }
}
</script>

<style scoped>
.form-cont {
    width: 352px;
    margin: 30px auto;
    text-align: left;
}
.btn-cont {
    text-align: center;
}
.btn-cont button {
    margin-right: 20px;
}
</style>



