<template>
    <div>
        <Button class="back-home" type="warning" size="large" to="/statistics">返回首页</Button>
        <Table class="data-table" :columns="dataHeader" :data="dataList" :loading="isLoading"></Table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isLoading: true,
            dataList: [],
            dataHeader: [
                {
                    key: 'linktime',
                    title: '创建日期',
                    width: 100,
                    align: 'center',
                    // render: (h, params) => {
                    //     let _this = this;
                    //     let createtime = new Date(params.row.createtime);
                    //     var o = {
                    //         "Y": createtime.getFullYear(),
                    //         "M": (createtime.getMonth() + 1) >= 10 ? String(createtime.getMonth() + 1) : '0' + (createtime.getMonth() + 1),
                    //         "D": createtime.getDate() >= 10 ? String(createtime.getDate()) : '0' + createtime.getDate(),
                    //         "h": createtime.getHours() > 10 ? createtime.getHours() : '0' + createtime.getHours(),
                    //         "m": createtime.getMinutes() > 10 ? createtime.getMinutes() : '0' + createtime.getMinutes(),
                    //         "s": createtime.getSeconds() > 10 ? createtime.getSeconds() : '0' + createtime.getSeconds()
                    //     }
                    //     let date = o.Y + "-" + o.M + "-" + o.D + " " + o.h + ":" + o.m + ":" + o.s
                    //     return h('div', {  
                    //         props: {},
                    //     },date)
                    // }
                }, {
                    key: 'qudao',
                    title: '渠道名称',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'fuwuhao',
                    title: '服务号名称',
                    // width: 90,
                    align: 'center'
                }, {
                    key: 'platform',
                    title: '小说平台',
                    width: 90,
                    align: 'center',
                    render: (h, params) => {
                        let _this = this;
                        let texts = '';
                        if(params.row.platform == 0){
                            texts = "推锐"
                        }else if(params.row.platform == 1){
                            texts = "点锐"
                        }else if(params.row.platform == 2){
                            texts = "要琪"
                        }else if(params.row.platform == 3){
                            texts = "一飞"
                        }else if(params.row.platform == 4){
                            texts = "有盟"
                        }else if(params.row.platform == 5){
                            texts = "无忧"
                        }else if(params.row.platform == 6){
                            texts = "袋鼠"
                        }
                        return h('div', {  
                            props: {},
                        },texts)
                    }
                }, {
                    key: 'uv',
                    title: '百度uv',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'yuedu',
                    title: '累计阅读人数',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'count_pay',
                    title: '付款数量',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'pay_rate',
                    title: '付款率',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'today_money',
                    title: '今日充值金额',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'money',
                    title: '累计充值金额',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'today_cost',
                    title: '今日成本',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'cost',
                    title: '总成本',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'today_back',
                    title: '动态回本率',
                    // width: 90,
                    align: 'center'
                }, {
                    key: 'back',
                    title: '回本率',
                    width: 90,
                    align: 'center'
                }
            ]
        }
    },
    mounted() {
        this.showDataList()
    },
    methods: {
        showDataList() {
            let url = sessionStorage.getItem("url");
            if(!url) {
                url = this.$route.params.url
                sessionStorage.setItem("url", this.$route.params.url);
            }
            this.$axios.get('/tongji', {params: {url: url}})
            .then(res => {
                this.isLoading = false
                this.dataList = res.data.data
            })
        }
    },
    destroyed() {
        sessionStorage.setItem("url", '');
    }
}
</script>

<style scoped>
.back-home {
    float: left;
    margin: 30px 0;
}

.data-table {
    clear: both;
}
</style>



