<template>
    <div class="index">
        <div class="index-page" v-if="$route.name == 'Index'">
            <Button class="create-btn" size="large" type="success" shape="circle" to="/statistics/createLink">
                新建统计链接
                <Icon type="ios-arrow-forward" />
            </Button>
            <Table class="index-table" stripe :columns="indexHeader" :data="indexData" :loading="isLoading"></Table>
            <Modal v-model="dialogModal" title="输入成本" @on-ok="submitCost" @on-cancel="cancel">
                <Form :model="costForm" :label-width="100">
                    <FormItem label="推广链接">
                        <Input v-model="costForm.url" disabled style="width: 300px" />
                    </FormItem>
                    <FormItem label="总成本">
                        <Input v-model="costForm.cost" placeholder="输入成本" style="width: 300px; margin-top: 10px;" />
                    </FormItem>
                    <FormItem label="记录成本时间">
                        <DatePicker v-model="createtime" @on-ok="confirmDate" type="datetime" placeholder="选择日期和时间" style="width: 200px"></DatePicker>
                    </FormItem>
                </Form>
            </Modal>
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogModal: false,
            isLoading: true,
            indexData: [],
            indexHeader: [
                {
                    title: '时间',
                    key: 'createtime',
                    width: 150,
                    align: 'center'
                }, {
                    title: '渠道名称',
                    key: 'qudao',
                    width: 120,
                    align: 'center'
                }, {
                    title: '服务号名称',
                    key: 'fuwuhao',
                    width: 150,
                    align: 'center'
                }, {
                    title: '小说平台',
                    key: 'platform',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        let _this = this;
                        let texts = '';
                        if(params.row.platform == 0){
                            texts = "推锐"
                        }else if(params.row.platform == 1){
                            texts = "点锐"
                        }else if(params.row.platform == 2){
                            texts = "要琪"
                        }else if(params.row.platform == 3){
                            texts = "一飞"
                        }else if(params.row.platform == 4){
                            texts = "有盟"
                        }else if(params.row.platform == 5){
                            texts = "无忧"
                        }else if(params.row.platform == 6){
                            texts = "袋鼠"
                        }
                        return h('div', {  
                            props: {},
                        },texts)
                    }
                }, {
                    title: '推广链接',
                    key: 'url',
                    width: 200,
                    align: 'center'
                }, {
                    title: '落地页链接',
                    key: 'out_url',
                    // width: 200,
                    align: 'center'
                }, {
                    title: 'Action',
                    key: 'action',
                    width: 320,
                    align: 'center',
                    render: (h, params) => {
                        let that = this
                        return h('div', [
                            h('Button', {
                                style: {
                                    'marginRight': '10px'
                                },
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                on: {
                                    click() {
                                        that.$router.push({path: '/statistics/updateLink', 
                                            name: 'updateLink',
                                            params: { 
                                                prevLinkForm: params.row
                                            }
                                        })
                                    }
                                }
                            }, '修改'),
                            h('Button', {
                                style: {
                                    'marginRight': '10px'
                                },
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click() {
                                        that.$axios.get('/link/del', {params: {id: params.row._id}})
                                        .then(res => {
                                            that.$Message.info('删除成功')
                                            that.indexData.splice(params.index, 1)
                                        })
                                    }
                                }
                            }, '删除'),
                            h('Button', {
                                style: {
                                    'marginRight': '10px'
                                },
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                on: {
                                    click() {
                                        that.dialogModal = true
                                        that.costForm.url = params.row.url
                                    }
                                }
                            }, '输入成本'),
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                on: {
                                    click() {
                                        that.$router.push({path: '/statistics/dataStatistics', 
                                            name: 'dataStatistics',
                                            params: { 
                                                url: params.row.url
                                            }
                                        })
                                    }
                                }
                            }, '查看统计数据')
                            
                        ])
                    }
                }
            ],
            costForm: {},
            createtime: null
        }
    },
    mounted() {
        this.showIndexData()
    },
    methods: {
        showIndexData() {
            this.$axios.get('/link').then(res => {
                this.indexData = res.data.data
                this.isLoading = false
            })
        },
        confirmDate() {
            var date = new Date(this.createtime).getTime();
            this.costForm.createtime = date
        },
        submitCost () {
            this.$axios.post('/cost/create', this.costForm)
            .then(res => {
                this.$Message.info('成本输入成功');
            })
        },
        cancel () {
            this.$Message.info('Clicked cancel');
        }
    },
    watch: {
        // '$route' (to, from) {
        //     if(to.name === 'Index') {
        //         this.indexPageShow = true
        //     } else if(to.name === "createLink" || to.name === "dataStatistics") {
        //         this.indexPageShow = false
        //     }
        // }
    }
}
</script>

<style scoped>
.create-btn {
    margin: 30px;
    float: right;
}

.index-table {
    clear: both;
}
</style>



