<template>
    <div class="update-link">
        <link-common @submitLinkForm="editLink" :prevLinkForm="prevLinkForm"></link-common>
    </div>
</template>

<script>
import linkCommon from './linkCommon/linkCommon';
export default {
    name: 'updateLink',
    components: {
        linkCommon
    },
    data() {
        return {
            prevLinkForm: {}
        }
    },
    beforeMount() {
        let form = sessionStorage.getItem("form");
        if(!form) {
            form = JSON.stringify(this.$route.params.prevLinkForm)
            sessionStorage.setItem("form", form);
        }
        this.prevLinkForm = JSON.parse(form)
        console.log(1, this.prevLinkForm)
    },
    methods: {
        editLink(linkForm) {
            this.$axios.post('/link/update', linkForm)
            .then(res => {
                this.$Message.info('链接修改成功')
                window.location.href = '/statistics'
            })
        }
    },
    destroyed() {
        sessionStorage.setItem("form", '');
    }
}
</script>



